@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif,"Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  background-color: black !important;
}

.nav-link {
  color: white !important;
}

.navbar-brand {
  color: white !important;
}

.navbar-nav {
  margin-left: auto;
}

.smallimg {
  height: 200px;
  width: 100% !important;
}

h1 {
  font-size: 20px !important;
}

.btn {
  background-color: black;
  color: white;
  box-shadow: none !important;
}

.bs {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  margin-top: 20px !important;
  border-radius: 5px !important;
  padding: 20px !important;
}

.bigimg {
  height: 400px;
  border-radius: 5px !important;
}

input, select, .ant-picker {
  box-shadow: none !important;
  margin-top: 10px;
  border: 1px solid black !important;
  width: 100% !important;
  border-radius: 3px !important;
  height: 35px !important;
}

h2 {
  font-size: 30px;
  padding: 10px;
  font-weight: bold;
  text-align: center;
  margin-top: 20px; /* Adds space between the tab content and the heading */
  margin-bottom: 20px; /* Adds space between the heading and the content */
}

.dropdown-toggle {
  border: 0px solid black !important;
}

.ant-picker-input input {
  border: none !important;
}

p {
  font-size: 16px !important;
}

.tab-content {
  margin-top: 20px; /* Space between the tab label and the heading */
}

.tab-heading {
  margin-bottom: 20px; /* Space between the heading and the content */
}

.landing{
  background-color: black !important;
  height: 100vh;
  padding-top:130px;
}

.landingbtn
{
  background-color: white !important;
  color: black !important;
  margin-top: 20px;
}

.landing .col-md-12{
  border-right:5px solid white !important;
}